<template>
  <div>
    <p>Home</p>
    <button @click="login">login</button>
  </div>
</template>

<script>
import axios from 'axios'
import queryString from "qs";
export default {
  name: "HomePage",
  beforeMount() {
    this.checkLineLoginCallBack();
  },
  methods: {
    login() {
      let URL = "https://access.line.me/oauth2/v2.1/authorize?";
      URL += "response_type=code";

      //channel id
      URL += "&client_id=1661441879";

      // 與Line Developer Channel設定的一樣就好，驗證完就會經過瀏覽器轉址回來
      URL += "&redirect_uri=https://qkqp3qr2.github.io/test2/";

      // 狀態碼是會從client呼叫端帶到驗證端，再從驗證端帶回
      // 因此應該可以作為一個亂數卡控確保是自   已丟出去的交易碼
      URL += "&state=abcde";

      //要求存取的範圍
      URL += "&scope=openid%20profile";
      window.location.href = URL;
      console.log("start login");
    },
    checkLineLoginCallBack() {
      console.log("checkLineLoginCallBack");
      const urlParams = new URLSearchParams(window.location.search);
      if (urlParams.has("state") && urlParams.has("code")) {
        const state = urlParams.get("state");
        const code = urlParams.get("code");
        if (state === "abcde") {
          const URL = "https://api.line.me/oauth2/v2.1/token?";
          const getTokenUrl = `${URL}`;
          const getTokenBody = queryString.stringify({
            grant_type: "authorization_code",
            code,
            redirect_uri: "https://qkqp3qr2.github.io/test2/",
            client_id: "1661441879",
            client_secret: "4f03d9065b14f7d6e394a0f20b0c53e3",
          });
          axios
            .post(getTokenUrl, getTokenBody)
            .then((e1) => {
              const token = e1.data.access_token;
              const idToken = e1.data.id_token;
              console.log(e1);
              const getProfileApiUrl = "https://api.line.me/v2/profile";
              axios({
                method: "GET",
                url: getProfileApiUrl,
                responseType: "json", // responseType 也可以寫在 header 裡面
                headers: {
                  Authorization: `Bearer ${token}`, // Bearer 跟 token 中間有一個空格
                },
              })
                .then((e2) => {
                  alert(
                    `line user id: ${e2.data.userId}, display name:${e2.data.displayName}`
                  );
                  console.log(JSON.stringify(e2));

                  const getVerifyApiUrl =
                    "https://api.line.me/oauth2/v2.1/verify";
                  const getVerifyBody = queryString.stringify({
                    client_id: "1656034758",
                    id_token: idToken,
                  });
                  axios
                    .post(getVerifyApiUrl, getVerifyBody)
                    .then((e3) => {
                      alert(`line email: ${e3.data.email}`);
                      console.log(JSON.stringify(e3));
                    })
                    .catch((error) => {
                      console.log(`錯誤: ${error}`);
                    });
                })
                .catch((error) => {
                  console.log(`錯誤: ${error}`);
                });
            })
            .catch((error) => {
              console.log(error);
              alert(error);
            });
        }
      }
    },
  },
};
</script>

