import { createRouter, createWebHistory } from 'vue-router'
import index from './pages/index.vue'
import Home from './pages/Home.vue'

export const router = createRouter({   
    history: createWebHistory(),
    routes: [
        {
            path: '/index',
            component: index
        },
        {
            path: '/home',
            component: Home
        }
    ]
});
